import { client } from '@ont-dev/ontology-dapi'
import React, { useState, useEffect, useCallback } from 'react'
import { utils } from 'ontology-ts-sdk'
import BigNumber from 'bignumber.js'
import { useAlert } from 'react-alert'
import { useMappedState, useDispatch } from 'redux-react-hook';
import { useFetchStakingTokens } from '../../hooks/token'
// import ReactTooltip from 'react-tooltip'
import { handleError } from '../../utils/errorHandle'
import { STAKING_ADDRESS } from '../../config'
import './index.css'

const { StringReader } = utils

const Staking = () => {
  const [stakeToken, setStakeToken] = useState({})
  const [stakingInfo, setStakingInfo] = useState({})
  const [amount, setAmount] = useState('')
  const [myStakes, setMyStakes] = useState([])
  const [stakeType, setStakeType] = useState('stake')
  const [showStakingModal, setShowStakingModal] = useState(false)
  const { account, loadingStakingToken, stopStakingInterval, stakingTokens } = useMappedState((state) => ({
    account: state.wallet.account,
    loadingStakingToken: state.common.loadingStakingToken,
    stopStakingInterval: state.common.stopStakingInterval,
    stakingTokens: state.common.stakingTokens
  }))
  const dispatch = useDispatch()
  const setModal = useCallback((modalType, modalDetail) => dispatch({ type: 'SET_MODAL', modalType, modalDetail }), [])
  const setStopStakingInterval = useCallback((stopStakingInterval) => dispatch({ type: 'SET_STOP_STAKING_INTERVAL', stopStakingInterval }), [])

  const Alert = useAlert()

  useEffect(() => {
    client.api.smartContract.invokeWasmRead({
      scriptHash: STAKING_ADDRESS,
      operation: 'staking_info',
      args: []
    })
    .then((infoStr) => {
      const info = {}
      const strReader = new StringReader(infoStr)
      info.amount = strReader.readUint128()
      info.period = strReader.readUint128()
      info.start_time = strReader.readUint128()
      info.settled_time = strReader.readUint128()
      info.interest = info.amount / info.period

      setStakingInfo(info)
    })
    .catch((e) => {
      handleError(e, (errorCode) => {
        if (errorCode === 'CONTRACT_ADDRESS_ERROR') {
          setStopStakingInterval(true)
        } else {
          console.log('get staking info', e)
        }
      })
    })
  }, [])

  useFetchStakingTokens()

  useEffect(() => {
    if (stakingTokens.length) {
      if (!stakeToken.id) {
        setStakeToken(stakingTokens[0])
      }
      getAccountStakes()
    }
  }, [stakingTokens])

  useEffect(() => {
    if (account && stakingTokens.length) {
      getAccountStakes()
      const interval = !stopStakingInterval && setInterval(getAccountStakes, 5000)
      return () => {
        interval && clearInterval(interval)
      }
    }
  }, [stakingTokens, account])

  useEffect(() => {
    if (!account) {
      setMyStakes([])
    }
  }, [account])

  function getAccountStakeByTokenId(id) {
    return client.api.smartContract.invokeWasmRead({
      scriptHash: STAKING_ADDRESS,
      operation: 'account_stake_info',
      args: [
        {
          type: 'Address',
          value: account
        },
        {
          type: 'Long',
          value: id
        }
      ]
    }).then((stakeStr) => {
      const strReader = new StringReader(stakeStr)
      return {
        id,
        balance: strReader.readUint128(),
        interest: strReader.readUint128()
      }
    })
  }

  function getAccountStakes() {
    if (account && stakingTokens.length) {
      Promise.all(stakingTokens.map((t) => getAccountStakeByTokenId(t.id)))
      .then((stakes) => {
        setMyStakes(stakes)
      })
      .catch((e) => {
        handleError(e, (errorCode) => {
          if (errorCode === 'CONTRACT_ADDRESS_ERROR') {
            setStopStakingInterval(true)
          } else {
            console.log('get account stakes', e)
          }
        })
      })
    }
  }

  async function onStake() {
    if (!account) {
      Alert.show('Please Connect Wallet First')
      return
    }
    if (stakeToken.id) {
      if (amount <= 0) {
        Alert.error('Amount should be greater than 0')
        return
      }
      if (stakeType === 'unstake' && amount > myStakes.find((s) => s.id === stakeToken.id).balance) {
        Alert.error('Amount should be less than your balance')
        return
      }
      try {
        const args = [
          {
            type: 'Address',
            value: account
          },
          {
            type: 'Long',
            value: stakeToken.id
          },
          {
            type: 'Long',
            value: new BigNumber(amount).times(new BigNumber(10 ** stakeToken.decimals)).integerValue(BigNumber.ROUND_DOWN).toString()
          }
        ]
        const stakeResult = await client.api.smartContract.invokeWasm({
          scriptHash: STAKING_ADDRESS,
          operation: stakeType,
          args,
          gasPrice: 2500,
          gasLimit: 3000000,
          requireIdentity: false
        })

        if (stakeResult.transaction) {
          setStakeType('stake')
          setShowStakingModal(false)
          setModal('infoModal', {
            show: true,
            type: 'success',
            text: `${stakeType === 'stake' ? 'Stake' : 'Unstake'} Successful`,
            extraText: 'View Transaction',
            extraLink: `https://explorer.ont.io/transaction/${stakeResult.transaction}`
          })
        }
      } catch (e) {
        setStakeType('stake')
        setShowStakingModal(false)
        setModal('infoModal', {
          show: true,
          type: 'error',
          text: `${stakeType === 'stake' ? 'Stake' : 'Unstake'} Failed`,
          extraText: `${e}`,
          extraLink: ''
        })
      }
    }
  }

  function renderLoading() {
    if (loadingStakingToken) {
      return (
        <div className="modal-overlay">
          <div className="modal-wrapper transparent-bg">
            <div className="modal-icon-loading modal-icon"></div>
            <div className="modal-text light">Loading...</div>
          </div>
        </div>
      )
    }
    return null
  }

  function onChooseToken(token) {
    if (!account) {
      Alert.show('Please Connect Wallet First')
      return
    }
    setStakeToken(token)
    setShowStakingModal(true)
  }

  function generateStakingPool() {
    if (stakingTokens.length) {
      return stakingTokens.map((token) => {
        const myStake = myStakes.find((s) => s.id === token.id) || {}
        return (
          <div className="pool-list-item" key={token.name}>
            <div className={`icon-${token.name} item-detail`}>
              <div className="token-name">{token.name}</div>
              <div className="token-balance">Total Staking<p>{new BigNumber(token.balance || 0).div(10 ** token.decimals).toString()}</p></div>
              {/* <div className="interest-rate">Interest Rate<p>{(stakingInfo.interest && token.weight) ? (stakingInfo.interest * token.weight / (10 ** 9)).toFixed(9) : 0} <span>UNX/s</span></p></div> */}
              <div className="staked-amount">Staked<p>{new BigNumber(myStake.balance || 0).div(10 ** token.decimals).toString()} <span>{token.name}</span></p></div>
              <div className="earned-amount">Earned<p>{new BigNumber(myStake.interest || 0).div(10 ** 9).toString()} <span>UNX</span></p></div>
              <div className="deposit-btn" onClick={() => onChooseToken(token)}>Deposit</div>
            </div>
          </div>
        )
      })
    }
  }

  return (
    <div className="stake-container">
      {renderLoading()}
      <div className="stake-pool">
        <div className="title"></div>
        <div className="pool-list">
          {generateStakingPool()}
        </div>
      </div>
      { showStakingModal ? (
        <div className="modal-overlay">
          <div className="modal-wrapper">
            <div className="close-btn" onClick={() => { setStakeType('stake'); setShowStakingModal(false) }}></div>
            <div className="stake-wrapper">
              <div className="stake-tabs">
                <div className={`${stakeType === 'stake' ? 'selected' : ''} stake-tab-item`} onClick={() => setStakeType('stake')}>Stake</div>
                <div className={`${stakeType === 'unstake' ? 'selected' : ''} stake-tab-item`} onClick={() => setStakeType('unstake')}>Unstake</div>
              </div>
              <div className={`icon-${stakeToken.name} token-placeholder`}></div>
              <div className="form-item">
                <div className="input-wrapper">
                  <input className="input inline-input" placeholder="Amount" type="number" onChange={(event) => setAmount(event.target.value)}></input>
                </div>
              </div>
              <div className="stake-btn" onClick={() => onStake()}>Confirm</div>
            </div>
          </div>
        </div>
      ) : null }
    </div>
  )
}

export default Staking