const initState = {
  loadingToken: true,
  stopInterval: false,
  tokens: [],
  loadingStakingToken: true,
  stopStakingInterval: false,
  stakingTokens: []
}

export const common = (state = initState, action) => {
  switch (action.type) {
    case 'SET_LOADING_TOKEN':
      return { ...state, loadingToken: action.loadingToken }
    case 'SET_STOP_INTERVAL':
      return { ...state, stopInterval: action.stopInterval }
    case 'SET_TOKENS':
      return { ...state, tokens: action.tokens }
    case 'SET_LOADING_STAKING_TOKEN':
      return { ...state, loadingStakingToken: action.loadingStakingToken }
    case 'SET_STOP_STAKING_INTERVAL':
      return { ...state, stopStakingInterval: action.stopStakingInterval }
    case 'SET_STAKING_TOKENS':
      return { ...state, stakingTokens: action.stakingTokens }
    default:
      return { ...state }
  }
}
